@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}button{width:100%}button:focus{outline:none}body,html{height:100%;font-family:'Roboto', sans-serif}#root{height:100%;display:flex;flex-direction:column}.header_fluid{background-color:#282d30}.header__logo{display:flex;justify-content:center}.header__logo-img{max-width:100%;margin:10px 0 -10px 0}.MuiFormLabel-root.Mui-focused{color:#999 !important}.manager-radio-form{margin-left:8px}.label{font-weight:700 !important;font-size:15px !important}table td{vertical-align:center}.product-list{min-width:100%;margin:100px 0 150px}.product-list__description{font-family:'Roboto', sans-serif;font-size:13px;color:#8b9497;margin-top:6px}@media (max-width: 720px){.product-list{margin:60px 0 80px}}.status-color{border:1px solid #e7e7e7;border-radius:50px;width:25px;height:25px;margin:0 auto}.title{text-align:center;font-family:'Roboto', sans-serif;font-weight:700;font-size:42px;margin:70px 0}@media (max-width: 720px){.title{margin:10px 0}}.not-found{min-height:100vh;display:flex;justify-content:center;align-items:center}.not-found__title{text-align:center;font-family:'Roboto', sans-serif;font-weight:700;font-size:80px;color:#000}.not-found__image{-webkit-animation:image-spin infinite 5s linear;animation:image-spin infinite 5